import React, { useState, useContext, useEffect, useRef, useMemo } from "react";
import "./Task13.css";
import { Context } from "../../Context";
import { HeadingText } from "../HeadingText/HeadingText";

import * as audio from "./sound";
import { houses, flowers, grass } from "./images";

// import {wardrobes } from "./img";
const {redHouse, blueHouse, greenHouse, yellowHouse} = houses;
const {redFlower, blueFlower, greenFlower, yellowFlower } = flowers;


const housesImages = [
  {
    id: 1,
    name: "redHouse",
    src: redHouse,
    tag: 'red'

  },
  {
    id: 2,
    name: "blueHouse",
    src: blueHouse,
    tag: 'blue'
  },
  {
    id: 3,
    name: "greenHouse",
    src: greenHouse,
    tag: 'green'
  },
  {
    id: 4,
    name: "yellowHouse",
    src: yellowHouse,
    tag: 'yellow'
  },
];

const flowersImages = [
  {
    id: 1,
    name: "redFlower",
    src: redFlower,
    tag: 'red'
  },
  {
    id: 2,
    name: "blueFlower",
    src: blueFlower,
    tag: 'blue'
  },
  {
    id: 3,
    name: "greenFlower",
    src: greenFlower,
    tag: 'green'
  },
  {
    id: 4,
    name: "yellowFlower",
    src: yellowFlower,
    tag: 'yellow'
  },
];

//    - 
const alphabetModalContent = () => {
  return (
    <>
      <h3> </h3>
      <br />
      <p>  </p>
    </>
  );
};

//    - 
const helpModalContent = () => {
  return (
    <>
      <h3> </h3>
      <br />
      <p></p>
    </>
  );
};

//   ( html )
// START
//   -   ( <>     </> )
const lessonHeading = () => (
  <>
    <p style={{ textAlign: "center" }}> </p>
  </>
);
//      ( <>     </> )
const themeHeading = () => (
  <>
    <p style={{ textAlign: "center" }}>
          ,    .
    </p>
  </>
);
//  title   (  html )
const titleHeading = " ";


function Task13() {
  const {
    helpSetIcon,
    alphabetSetIcon,
    helpSetModalContent,
    alphabetSetModalContent,
    lessonSetStartAction,
    lessonSetResetTask,
    resetTask,
    lessonSetCheckTask,
    checkTask,
    lessonSetAndAnswer,
    lessonSetViewFeedback,
  } = useContext(Context);

  //     - (  )
  useEffect(() => {
    //  helpSetIcon -    ( - true,  - false)
    helpSetIcon(true);
    //  alphabetSetIcon -   ( - true,  - false)
    alphabetSetIcon(true);
    helpSetModalContent(helpModalContent);
    alphabetSetModalContent(alphabetModalContent);
  }, []);

  useEffect(() => {
    if (resetTask === true) {
      dropPlace.current.childNodes.forEach((item) => {
        if (item.childNodes.length > 2) {
          dragPlace.current.appendChild(item.childNodes[2]);
        }
      });
      lessonSetResetTask(false);
    }
  }, [resetTask]);

  useEffect(() => {
    if (checkTask === true) {
      let winVar = 0;
      dropPlace.current.childNodes.forEach((item) => {
        if (item.childNodes.length === 3) {
          winVar+=1;
        }
      });
      if (winVar === flowersImages.length) {
        lessonSetAndAnswer(true);
      } else lessonSetAndAnswer(false);
    }
    lessonSetCheckTask(false);
  }, [checkTask]);

  const dragPlace = useRef(null);
  const dropPlace = useRef(null);
  const [draggingElem, setDruggingElem] = useState();

  const housesImagesForMatchingReady = useMemo(() => {
    return housesImages.sort(() => (Math.random() > 0.5 ? 1 : -1));
  }, []);
  const flowersImagesForMatchingReady = useMemo(() => {
    return flowersImages.sort(() => (Math.random() > 0.5 ? 1 : -1));
  }, []);

  let draggingItem;
  let elemBelow;
  function mouseDown(event) {
    draggingItem = event.target;
    draggingItem.style.cursor = "grabbing";
    let shiftX = event.clientX - draggingItem.getBoundingClientRect().left;
    let shiftY = event.clientY - draggingItem.getBoundingClientRect().top;

    draggingItem.style.position = "absolute";
    draggingItem.style.zIndex = 1000;
    document.body.appendChild(draggingItem);

    moveAt(event.pageX, event.pageY);

    function moveAt(pageX, pageY) {
      draggingItem.style.left = pageX - shiftX + "px";
      draggingItem.style.top = pageY - shiftY + "px";
    }

    elemBelow = document.elementFromPoint(event.clientX, event.clientY);

    function onMouseMove(event) {
      moveAt(event.pageX, event.pageY);

      draggingItem.hidden = true;
      elemBelow = document.elementFromPoint(event.clientX, event.clientY);
      draggingItem.hidden = false;

      if (!elemBelow) return;
    }
    document.addEventListener("mousemove", onMouseMove);

    draggingItem.onmouseup = function () {
      document.removeEventListener("mousemove", onMouseMove);
      draggingItem.style.cursor = "grab";

      if (
        (elemBelow.classList.contains("dropImages_houses") || elemBelow.classList.contains("dropGrass_houses"))
         &&
        elemBelow.attributes.getNamedItem("drop-data").value ===
          draggingItem.attributes.getNamedItem("drag-data").value
      ) {
        draggingItem.style.position = "relative";
        draggingItem.style.zIndex = null;
        draggingItem.style.top = null;
        draggingItem.style.left = null;
        elemBelow.parentElement.append(draggingItem);

        lessonSetStartAction(true);
      } else {
        draggingItem.style.position = "relative";
        draggingItem.style.zIndex = null;
        draggingItem.style.top = null;
        draggingItem.style.left = null;
        dragPlace.current.appendChild(draggingItem);
      }
      setDruggingElem(draggingItem);
    };
  }

  //      
  return (
    <div className="lessonContent">
      <HeadingText
        lessonHeading={lessonHeading()}
        themeHeading={themeHeading()}
        // titleHeading={titleHeading}
        soundSource={audio.titleSound}
        soundVisible={true}
        titleVisible={false}
      />
      <div className="comparePicturesWrapper">
        <div className="dropPlaceWrapper_houses" ref={dropPlace}>
          {housesImagesForMatchingReady.map((house) => (
            <div className="dropPlace_houses" key={house.id}>
              <img
                src={house.src}
                alt={house.name}
                className="dropImages_houses"
                draggable="false"
                drop-data={house.tag}
                />
              <img
              src={grass}
              alt={'grass'}
              drop-data={house.tag}
              className="dropGrass_houses"
              />
            </div>
          ))}
        </div>
        <div className="dragPlaceWrapper_houses" ref={dragPlace}>
          {flowersImagesForMatchingReady.map((flower) => (
              <img
                key={flower.id}
                src={flower.src}
                alt={flower.name}
                className="dragImages_houses"
                draggable="false"
                onMouseDown={mouseDown}
                drag-data={flower.tag}
              />
          ))}
        </div>
      </div>
    </div>
  );
}

export default Task13;
