import React, { useState, useContext, useEffect, useRef, useMemo } from "react";
import "./Task1.css";
import { Context } from "../../Context";
import { HeadingText } from "../HeadingText/HeadingText";

import lion from "./img/lion.png";
import tiger from "./img/tiger.png";
import monkey from "./img/monkey.png";


//    - 
const alphabetModalContent = () => {
    return (
        <>
            <h3> </h3>
            <br />
            <p>  </p>
        </>
    );
};

//    - 
const helpModalContent = () => {
    return (
        <>
            <h3> </h3>
            <br />
            <p>     </p>
        </>
    );
};

//   ( html )
// START
//   -   ( <>     </> )
const lessonHeading = () => (
    <>
        <p>  </p>
    </>
);
//      ( <>     </> )
const themeHeading = () => (
    <>
        <p style={{ textAlign: 'center' }}>   .   ? ?<br />    . ( )</p>
    </>
);
//  title   (  html )
const titleHeading = " ";

const picturesForMatching = [
    {
        id: 'pic_1',
        data: 'lion',
        src: lion
    },
    {
        id: 'pic_2',
        data: 'lion',
        src: lion
    },
    {
        id: 'pic_3',
        data: 'tiger',
        src: tiger
    },
    {
        id: 'pic_4',
        data: 'tiger',
        src: tiger
    },
    {
        id: 'pic_5',
        data: 'tiger',
        src: tiger
    },
    {
        id: 'pic_6',
        data: 'monkey',
        src: monkey
    },
    {
        id: 'pic_7',
        data: 'tiger',
        src: tiger
    },
    {
        id: 'pic_8',
        data: 'lion',
        src: lion
    },
];
const winVar = 'monkey';

const PictureChooser = ({ picturesForMatching, matchingHandler, picturesBlock }) => {


    return (
        <div className="oneChoisePicture" ref={picturesBlock}>
            {picturesForMatching.map(item => (
                <div key={item.id} className="oneChoisePictureCard" data={item.data} onClick={matchingHandler}><img src={item.src} alt="" draggable="false" /></div>
            ))}
        </div>
    );
}

function Task1() {
    const {
        helpSetIcon,
        alphabetSetIcon,
        helpSetModalContent,
        alphabetSetModalContent,
        lessonSetStartAction,
        lessonSetResetTask,
        resetTask,
        lessonSetCheckTask,
        checkTask,
        lessonSetAndAnswer
    } = useContext(Context);

    //     - (  )
    useEffect(() => {
        //  helpSetIcon -    ( - true,  - false)
        helpSetIcon(true);
        //  alphabetSetIcon -   ( - true,  - false)
        alphabetSetIcon(true);
        helpSetModalContent(helpModalContent);
        alphabetSetModalContent(alphabetModalContent);
    }, []);

    const [matchedItem, setMatchedItem] = useState();
    const picturesBlock = useRef(null);

    useEffect(() => {
        if (resetTask === true) {

            picturesBlock.current.childNodes.forEach(item => {
                item.classList.remove('oneChoisePictureCardActive');
            });

        }
        lessonSetResetTask(false);
    }, [resetTask]);

    useEffect(() => {
        if (checkTask === true) {
            if (matchedItem.attributes.getNamedItem('data').value === winVar) {
                lessonSetAndAnswer(true);
            }
            else {
                lessonSetAndAnswer(false);
            }
        }
        lessonSetCheckTask(false);
    }, [checkTask]);


    const matchingHandler = (event) => {
        const matchedItem = event.target;
        matchedItem.parentElement.childNodes.forEach(item => {
            item.classList.remove('oneChoisePictureCardActive');
        });
        matchedItem.classList.add('oneChoisePictureCardActive');
        setMatchedItem(matchedItem);
        lessonSetStartAction(true);
    };

    const picturesReady = useMemo(() => {
        return picturesForMatching.sort(() => Math.random() > 0.5 ? 1 : -1);
    }, []);


    //      
    return (
        <div className="lessonContent">
            <HeadingText
                lessonHeading={lessonHeading()}
                themeHeading={themeHeading()}
                // titleHeading={titleHeading}
                // soundSource={soundSource}
                soundVisible={false}
                titleVisible={false}
            />
            <div className="oneChoisePictureWrapper">

                <PictureChooser picturesForMatching={picturesReady} matchingHandler={matchingHandler} picturesBlock={picturesBlock} />

            </div>
        </div>
    );
}

export default Task1;
