import React, { useState, useContext, useEffect, useRef, useMemo } from "react";
import "./Task14.css";
import { Context } from "../../Context";
import { HeadingText } from "../HeadingText/HeadingText";

import horn from "./img/horn.png";
import ram from "./img/ram.png";
import apron from "./img/apron.png";
import woman from "./img/woman.png";
import tail from "./img/tail.png";
import rooster from "./img/rooster.png";
//    - 
const alphabetModalContent = () => {
    return (
        <>
            <h3> </h3>
            <br />
            <p>  </p>
        </>
    );
};

//    - 
const helpModalContent = () => {
    return (
        <>
            <h3> </h3>
            <br />
            <p></p>

        </>
    );
};

//   ( html )
// START
//   -   ( <>     </> )
const lessonHeading = () => (
    <>
        <p>  ?</p>
    </>
);
//      ( <>     </> )
const themeHeading = () => (
    <>
        <p style={{ textAlign: 'center' }}>
               
        </p>
    </>
);
//  title   (  html )
const titleHeading = " ";



const Images = ({ itemsToImage }) => (
    <>
        {itemsToImage.map(image => (
            <div key={image.id} className="itemsToImage_imageWrapper" drop-data={image.data}><img src={image.srcImage} alt="" className="itemsToImage_image" draggable="false" /></div>
        ))}
    </>
);

const Items = ({ itemsToImageReady, onMouseDown }) => (
    <>
        {itemsToImageReady.map(item => (
            <img key={item.id} src={item.srcItem} alt="" className="itemsToImage_item" drag-data={item.data} onMouseDown={onMouseDown} draggable="false" />
        ))}
    </>
);

function Task14() {
    const {
        helpSetIcon,
        alphabetSetIcon,
        helpSetModalContent,
        alphabetSetModalContent,
        lessonSetStartAction,
        lessonSetFinishAction,
        lessonSetResetTask,
        resetTask,
        lessonSetCheckTask,
        checkTask,
        lessonSetAndAnswer,
        lessonSetViewFeedback
    } = useContext(Context);

    //     - (  )
    useEffect(() => {
        //  helpSetIcon -    ( - true,  - false)
        helpSetIcon(true);
        //  alphabetSetIcon -   ( - true,  - false)
        alphabetSetIcon(true);
        helpSetModalContent(helpModalContent);
        alphabetSetModalContent(alphabetModalContent);
    }, []);

    const dragPlace = useRef(null);
    const dropPlace = useRef(null);
    const [draggingElem, setDraggingElem] = useState();

    useEffect(() => {
        if (resetTask === true) {
            dropPlace.current.childNodes.forEach(item => {
                if (item.childNodes.length > 1) {
                    item.childNodes[1].style.visibility = 'visible';
                    dragPlace.current.appendChild(item.childNodes[1]);

                }
            })
            lessonSetResetTask(false);
        }
    }, [resetTask]);

    useEffect(() => {
        let winCount = 0;
        dropPlace.current.childNodes.forEach(item => {
            if (item.childNodes.length > 1) {
                winCount++;
            }
        });
        if (winCount === 3) {
            lessonSetViewFeedback(true);
            lessonSetAndAnswer(true);
        }
    }, [draggingElem]);

    const itemsToImage = [
        {
            id: 'itemToImage_1',
            data: 'horn',
            srcItem: horn,
            srcImage: ram
        },
        {
            id: 'itemToImage_3',
            data: 'tail',
            srcItem: tail,
            srcImage: rooster
        },
        {
            id: 'itemToImage_2',
            data: 'apron',
            srcItem: apron,
            srcImage: woman
        }
    ];

    let draggingItem;
    let elemBelow;
    function mouseDown(event) {
        draggingItem = event.target;
        draggingItem.style.cursor = 'grabbing';
        let shiftX = event.clientX - draggingItem.getBoundingClientRect().left;
        let shiftY = event.clientY - draggingItem.getBoundingClientRect().top;

        draggingItem.style.position = 'absolute';
        draggingItem.style.zIndex = 1000;
        document.body.appendChild(draggingItem);

        moveAt(event.pageX, event.pageY);

        function moveAt(pageX, pageY) {
            draggingItem.style.left = pageX - shiftX + 'px';
            draggingItem.style.top = pageY - shiftY + 'px';
        }

        elemBelow = document.elementFromPoint(event.clientX, event.clientY);

        function onMouseMove(event) {

            moveAt(event.pageX, event.pageY);

            draggingItem.hidden = true;
            elemBelow = document.elementFromPoint(event.clientX, event.clientY);
            draggingItem.hidden = false;

            if (!elemBelow) return;
        }
        document.addEventListener('mousemove', onMouseMove);

        draggingItem.onmouseup = function () {
            document.removeEventListener('mousemove', onMouseMove);
            draggingItem.style.cursor = 'grab';
            if (elemBelow.classList.contains('itemsToImage_imageWrapper') && elemBelow.attributes.getNamedItem('drop-data').value === draggingItem.attributes.getNamedItem('drag-data').value) {

                draggingItem.style.position = 'relative ';
                draggingItem.style.zIndex = null;
                draggingItem.style.top = null;
                draggingItem.style.left = null;
                draggingItem.style.visibility = 'hidden';
                elemBelow.appendChild(draggingItem);

                lessonSetFinishAction(true);

            }
            else {
                draggingItem.style.position = 'relative ';
                draggingItem.style.zIndex = null;
                draggingItem.style.top = null;
                draggingItem.style.left = null;
                dragPlace.current.appendChild(draggingItem);
            }
            setDraggingElem(draggingItem);
        };
    };


    const itemsToImageReady = useMemo(() => {
        return itemsToImage.sort(() => Math.random() > 0.5 ? 1 : -1);
    }, []);


    //      
    return (
        <div className="lessonContent">
            <HeadingText
                lessonHeading={lessonHeading()}
                themeHeading={themeHeading()}
                // titleHeading={titleHeading}
                // soundSource={soundSource}
                soundVisible={false}
                titleVisible={false}
            />

            <div className="matchItemToImageWrappere" >

                <div className="matchItemToImage_images" ref={dropPlace}>
                    <Images itemsToImage={itemsToImage} />
                </div>

                <div className="matchItemToImage_items" ref={dragPlace}>
                    <Items itemsToImageReady={itemsToImageReady} onMouseDown={mouseDown} />

                </div>
            </div>

        </div>
    );
}

export default Task14;
