import React, { useState, useContext, useEffect, useRef, useMemo } from "react";
import "./Task11.css";
import { Context } from "../../Context";
import { HeadingText } from "../HeadingText/HeadingText";

import * as audio from "./sound";
// import {{girlWithPyramid, boyWithCar, girlWithApple, boyWithPear}, {wardrobeWithPear, wardrobeWithCar, wardrobeWithPyramid, wardrobeWithApple }} from "./img";
import { childrens, wardrobes } from "./img";

// import {wardrobes } from "./img";
const { girlWithPyramid, boyWithCar, girlWithApple, boyWithPear } = childrens;
const {
  wardrobeWithPear,
  wardrobeWithCar,
  wardrobeWithPyramid,
  wardrobeWithApple,
} = wardrobes;


const childrenImages = [
  {
    id: 1,
    name: "boyWithCar",
    src: boyWithCar,
    tag: 'car'

  },
  {
    id: 2,
    name: "girlWithPyramid",
    src: girlWithPyramid,
    tag: 'pyramid'
  },
  {
    id: 3,
    name: "girlWithApple",
    src: girlWithApple,
    tag: 'apple'
  },
  {
    id: 4,
    name: "boyWithPear",
    src: boyWithPear,
    tag: 'pear'
  },
];

const wardrobesImages = [
  {
    id: 1,
    name: "wardrobeWithPear",
    src: wardrobeWithPear,
    tag: 'pear'
  },
  {
    id: 2,
    name: "wardrobeWithCar",
    src: wardrobeWithCar,
    tag: 'car'
  },
  {
    id: 3,
    name: "wardrobeWithPyramid",
    src: wardrobeWithPyramid,
    tag: 'pyramid'
  },
  {
    id: 4,
    name: "wardrobeWithApple",
    src: wardrobeWithApple,
    tag: 'apple'
  },
];

//    - 
const alphabetModalContent = () => {
  return (
    <>
      <h3> </h3>
      <br />
      <p>  </p>
    </>
  );
};

//    - 
const helpModalContent = () => {
  return (
    <>
      <h3> </h3>
      <br />
      <p></p>
    </>
  );
};

//   ( html )
// START
//   -   ( <>     </> )
const lessonHeading = () => (
  <>
    <p style={{ textAlign: "center" }}>  </p>
  </>
);
//      ( <>     </> )
const themeHeading = () => (
  <>
    <p style={{ textAlign: "center" }}>
             .
    </p>
  </>
);
//  title   (  html )
const titleHeading = " ";


function Task11() {
  const {
    helpSetIcon,
    alphabetSetIcon,
    helpSetModalContent,
    alphabetSetModalContent,
    lessonSetStartAction,
    lessonSetResetTask,
    resetTask,
    lessonSetCheckTask,
    checkTask,
    lessonSetAndAnswer,
    lessonSetViewFeedback,
  } = useContext(Context);

  //     - (  )
  useEffect(() => {
    //  helpSetIcon -    ( - true,  - false)
    helpSetIcon(true);
    //  alphabetSetIcon -   ( - true,  - false)
    alphabetSetIcon(true);
    helpSetModalContent(helpModalContent);
    alphabetSetModalContent(alphabetModalContent);
  }, []);

  useEffect(() => {
    if (resetTask === true) {
      dropPlace.current.childNodes.forEach((item) => {
        if (item.childNodes.length > 1) {
          dragPlace.current.appendChild(item.childNodes[1]);
        }
      });
      lessonSetResetTask(false);
    }
  }, [resetTask]);

  useEffect(() => {
    if (checkTask === true) {
      let winVar = 0;
      dropPlace.current.childNodes.forEach((item) => {
        if (item.childNodes.length === 2) {
          winVar+=1;
        }
      });
      if (winVar === childrenImages.length) {
        lessonSetAndAnswer(true);
      } else lessonSetAndAnswer(false);
    }
    lessonSetCheckTask(false);
  }, [checkTask]);

  const dragPlace = useRef(null);
  const dropPlace = useRef(null);
  const [draggingElem, setDruggingElem] = useState();

  const wardrobesImagesForMatchingReady = useMemo(() => {
    return wardrobesImages.sort(() => (Math.random() > 0.5 ? 1 : -1));
  }, []);
  const childrenImagesForMatchingReady = useMemo(() => {
    return childrenImages.sort(() => (Math.random() > 0.5 ? 1 : -1));
  }, []);

  let draggingItem;
  let elemBelow;
  function mouseDown(event) {
    draggingItem = event.target;
    draggingItem.style.cursor = "grabbing";
    let shiftX = event.clientX - draggingItem.getBoundingClientRect().left;
    let shiftY = event.clientY - draggingItem.getBoundingClientRect().top;

    draggingItem.style.position = "absolute";
    draggingItem.style.zIndex = 1000;
    document.body.appendChild(draggingItem);

    moveAt(event.pageX, event.pageY);

    function moveAt(pageX, pageY) {
      draggingItem.style.left = pageX - shiftX + "px";
      draggingItem.style.top = pageY - shiftY + "px";
    }

    elemBelow = document.elementFromPoint(event.clientX, event.clientY);

    function onMouseMove(event) {
      moveAt(event.pageX, event.pageY);

      draggingItem.hidden = true;
      elemBelow = document.elementFromPoint(event.clientX, event.clientY);
      draggingItem.hidden = false;

      if (!elemBelow) return;
    }
    document.addEventListener("mousemove", onMouseMove);

    draggingItem.onmouseup = function () {
      document.removeEventListener("mousemove", onMouseMove);
      draggingItem.style.cursor = "grab";

      if (
        elemBelow.classList.contains("dropWardrobeImages") &&
        elemBelow.attributes.getNamedItem("drop-data").value ===
          draggingItem.attributes.getNamedItem("drag-data").value
      ) {
        draggingItem.style.position = "relative";
        draggingItem.style.zIndex = null;
        draggingItem.style.top = null;
        draggingItem.style.left = null;
        elemBelow.parentElement.append(draggingItem);

        lessonSetStartAction(true);
      } else {
        draggingItem.style.position = "relative";
        draggingItem.style.zIndex = null;
        draggingItem.style.top = null;
        draggingItem.style.left = null;
        dragPlace.current.appendChild(draggingItem);
      }
      setDruggingElem(draggingItem);
    };
  }

  //      
  return (
    <div className="lessonContent">
      <HeadingText
        lessonHeading={lessonHeading()}
        themeHeading={themeHeading()}
        // titleHeading={titleHeading}
        soundSource={audio.titleSound}
        soundVisible={true}
        titleVisible={false}
      />
      <div className="comparePicturesWrapper">
        <div className="dropPlaceWrapper_wardrobes" ref={dropPlace}>
          {wardrobesImagesForMatchingReady.map((wardrobe) => (
            <div className="dropPlace_wardrobes" key={wardrobe.id}>

              <img
                src={wardrobe.src}
                alt={wardrobe.name}
                className="dropWardrobeImages"
                draggable="false"
                drop-data={wardrobe.tag}
              />
            </div>
          ))}
        </div>
        <div className="dragPlaceWrapper_wardrobes" ref={dragPlace}>
          {childrenImagesForMatchingReady.map((child) => (
              <img
                key={child.id}
                src={child.src}
                alt={child.name}
                className="dragWardrobeImages"
                draggable="false"
                onMouseDown={mouseDown}
                drag-data={child.tag}
              />
          ))}
        </div>
      </div>
    </div>
  );
}

export default Task11;
