import React, { useState, useContext, useEffect, useRef, useMemo } from "react";
import "./Task5.css";
import { Context } from "../../Context";
import { HeadingText } from "../HeadingText/HeadingText";

import lake from './img/lake.png';
import rock from './img/rock.png';
import stone from './img/stone.png';
import tree from './img/tree.png';
import bush from './img/bush.png';

import elephant from './img/elephant.png';
import giraffe from './img/giraffe.png';
import lion from './img/lion.png';
import rhino from './img/rhino.png';
import tiger from './img/tiger.png';

//    - 
const alphabetModalContent = () => {
    return (
        <>
            <h3> </h3>
            <br />
            <p>  </p>
        </>
    );
};

//    - 
const helpModalContent = () => {
    return (
        <>
            <h3> </h3>
            <br />
            <p>     </p>
        </>
    );
};

//   ( html )
// START
//   -   ( <>     </> )
const lessonHeading = () => (
    <>
        <p> ?</p>
    </>
);
//      ( <>     </> )
const themeHeading = () => (
    <>
        <p style={{ textAlign: 'center' }}>,     .     .
        </p>
    </>
);
//  title   (  html )
const titleHeading = " ";

const picturesForMatching = [
    {
        id: 'pic_1',
        data: 'elephant',
        src: elephant
    },
    {
        id: 'pic_2',
        data: 'giraffe',
        src: giraffe
    },
    {
        id: 'pic_3',
        data: 'tiger',
        src: tiger
    },
    {
        id: 'pic_4',
        data: 'lion',
        src: lion
    },
    {
        id: 'pic_5',
        data: 'rhino',
        src: rhino
    }
];

function Task5() {
    const {
        helpSetIcon,
        alphabetSetIcon,
        helpSetModalContent,
        alphabetSetModalContent,
        lessonSetStartAction,
        lessonSetResetTask,
        resetTask,
        lessonSetCheckTask,
        checkTask,
        lessonSetAndAnswer,
        lessonSetViewFeedback
    } = useContext(Context);
    //     - (  )
    useEffect(() => {
        //  helpSetIcon -    ( - true,  - false)
        helpSetIcon(true);
        //  alphabetSetIcon -   ( - true,  - false)
        alphabetSetIcon(true);
        helpSetModalContent(helpModalContent);
        alphabetSetModalContent(alphabetModalContent);
    }, []);
    const dragPlace = useRef(null);
    const [matchedItem, setMatchedItem] = useState();

    const [actualImg, setActualImg] = useState([
        {
            data: 'elephant',
            src: rock
        },
        {
            data: 'giraffe',
            src: tree
        },
        {
            data: 'tiger',
            src: bush
        },
        {
            data: 'lion',
            src: stone
        },
        {
            data: 'rhino',
            src: lake
        }
    ]);
    const [imgCount, setImgCount] = useState(0);

    useEffect(() => {
        if (resetTask === true) {
            // dragPlace.current.childNodes.forEach(item => item.classList.remove('dragPictureActive'));
        }
        lessonSetResetTask(false);
    }, [resetTask]);

    useEffect(() => {
        if (checkTask === true) {
            // if (matchedItem.attributes.getNamedItem('data').value === winVar) {
            //     lessonSetAndAnswer(true);
            // }
            // else {
            //     lessonSetAndAnswer(false);
            // }
        }
        lessonSetCheckTask(false);
    }, [checkTask]);

    const lastImg = actualImg.length - 1;
    let draggingItem;
    let elemBelow;

    function mouseDown(event) {
        draggingItem = event.target;
        draggingItem.style.cursor = 'grabbing';
        let shiftX = event.clientX - draggingItem.getBoundingClientRect().left;
        let shiftY = event.clientY - draggingItem.getBoundingClientRect().top;

        draggingItem.style.position = 'absolute';
        draggingItem.style.zIndex = 1000;
        document.body.appendChild(draggingItem);

        moveAt(event.pageX, event.pageY);

        function moveAt(pageX, pageY) {
            draggingItem.style.left = pageX - shiftX + 'px';
            draggingItem.style.top = pageY - shiftY + 'px';
        }

        elemBelow = document.elementFromPoint(event.clientX, event.clientY);

        function onMouseMove(event) {

            moveAt(event.pageX, event.pageY);

            draggingItem.hidden = true;
            elemBelow = document.elementFromPoint(event.clientX, event.clientY);
            draggingItem.hidden = false;

            if (!elemBelow) return;
        }
        document.addEventListener('mousemove', onMouseMove);

        draggingItem.onmouseup = function () {
            document.removeEventListener('mousemove', onMouseMove);
            draggingItem.style.cursor = 'grab';
            if (elemBelow.classList.contains('dropImg') && draggingItem.attributes.getNamedItem('drag-data').value === elemBelow.attributes.getNamedItem('drop-data').value) {
                imgChanger();
                styleChanger();
                draggingItem.style.display = 'none';

            }
            else {
                styleChanger();
            }
        };
    };

    const picturesReady = useMemo(() => {
        return picturesForMatching.sort(() => Math.random() > 0.5 ? 1 : -1);
    }, []);

    const imgChanger = () => {

        if (imgCount < lastImg) {
            setImgCount(imgCount + 1)
        }
        if (imgCount === lastImg) {
            lessonSetViewFeedback(true);
            lessonSetAndAnswer(true);

        }
    }
    const styleChanger = () => {
        draggingItem.style.position = 'relative ';
        draggingItem.style.zIndex = null;
        draggingItem.style.top = null;
        draggingItem.style.left = null;
        dragPlace.current.appendChild(draggingItem);
    }
    //      
    return (
        <div className="lessonContent">
            <HeadingText
                lessonHeading={lessonHeading()}
                themeHeading={themeHeading()}
                // titleHeading={titleHeading}
                // soundSource={soundSource}
                soundVisible={false}
                titleVisible={false}
            />
            <div className="dnd_matchingWrapper">
                <div className="dropPlace" >

                    <img src={actualImg[imgCount].src} alt="" className="dropImg" draggable="false" drop-data={actualImg[imgCount].data} />

                </div>
                <div className="dragPlace" ref={dragPlace}>
                    {picturesReady.map(picture => (
                        <img key={picture.id} drag-data={picture.data} className="dragPicture" src={picture.src} alt="" onMouseDown={mouseDown} draggable="false" />
                    ))}
                </div>
            </div>

        </div>
    );
}

export default Task5;
