const Pencils = ({ clickHandler }) => (
    <>
        {pencils.map(pencil => (
            <img key={pencil.id} src={pencil.src} alt="" className="pencil" onClick={clickHandler} data-color={pencil.data} />
        ))}
    </>
);


function Task9() {
    const {
        helpSetIcon,
        alphabetSetIcon,
        helpSetModalContent,
        alphabetSetModalContent,
        lessonSetStartAction,
        lessonSetResetTask,
        resetTask,
        lessonSetCheckTask,
        checkTask,
        lessonSetAndAnswer,
        lessonSetViewFeedback,
        lessonSetFinishAction
    } = useContext(Context);

    //     - (  )
    useEffect(() => {
        //  helpSetIcon -    ( - true,  - false)
        helpSetIcon(true);
        //  alphabetSetIcon -   ( - true,  - false)
        alphabetSetIcon(true);
        helpSetModalContent(helpModalContent);
        alphabetSetModalContent(alphabetModalContent);
    }, []);

    const pancilsWrapper = useRef(null);
    const coloringImage = useRef(null);
    const [pancilColor, setPencilColor] = useState('transperent');


    useEffect(() => {
        if (resetTask === true) {

            console.log(coloringImage.current.childNodes);
            coloringImage.current.childNodes.forEach(child => {
                if (child.id != 'circuit') child.style.fill = '#ffffff';
            });
            lessonSetResetTask(false);
        }
    }, [resetTask]);

    const clickHandler = (event) => {
        pancilsWrapper.current.childNodes.forEach(pancil => {
            pancil.classList.remove('pencilActive')
        });
        event.target.classList.add('pencilActive');


        setPencilColor(event.target.attributes.getNamedItem('data-color').value);


    }


    const changeFillSvg = (event) => {
        lessonSetFinishAction(true);
        if (event.target.classList.contains('coloringImage')) return
        event.target.style.fill = pancilColor;
    }


    //      
    return (
        <div className="lessonContent">
            <HeadingText
                lessonHeading={lessonHeading()}
                themeHeading={themeHeading()}
                // titleHeading={titleHeading}
                // soundSource={soundSource}
                soundVisible={false}
                titleVisible={false}
            />
            <div className="coloringsWrapper" >

                <div className="coloringImageWrapper" >
                    <ColoringImage changeFillSvg={changeFillSvg} coloringImage={coloringImage} />
                </div>

                <div className="pancilsWrapper" ref={pancilsWrapper}>
                    <Pencils clickHandler={clickHandler} />
                </div>

            </div>

        </div>
    );
}

export default Task9;
