function Task10() {
  const {
    helpSetIcon,
    alphabetSetIcon,
    helpSetModalContent,
    alphabetSetModalContent,
    lessonSetStartAction,
    lessonSetResetTask,
    resetTask,
    lessonSetCheckTask,
    checkTask,
    lessonSetAndAnswer,
    lessonSetViewFeedback,
  } = useContext(Context);

  //     - (  )
  useEffect(() => {
    //  helpSetIcon -    ( - true,  - false)
    helpSetIcon(true);
    //  alphabetSetIcon -   ( - true,  - false)
    alphabetSetIcon(true);
    helpSetModalContent(helpModalContent);
    alphabetSetModalContent(alphabetModalContent);
  }, []);

  const [currentAudio, setCurrentAudio] = useState(undefined);

  const onImgClick = (e) => {
    playSound(audio[e.target.id], setCurrentAudio, currentAudio)
  }



  //      
  return (
    <div className="lessonContent">
      <HeadingText
        lessonHeading={lessonHeading()}
        themeHeading={themeHeading()}
        // titleHeading={titleHeading}
        soundSource={audio.titleSound}
        soundVisible={true}
        titleVisible={false}
      />
      <div onClick={onImgClick} className="imageWrapper">
        <ChildRoom />
      </div>
    </div>
  );
}

export default Task10;
